@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
:root {
  --box-shadow-main: 0px 3px 15px 1px rgba(56, 56, 116, 0.3);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

html {
  font-size: 62.5%;
}

@media only screen and (max-width: 1400px) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (max-width: 1200px) {
  html {
    font-size: 50%;
  }
}

@media only screen and (max-width: 992px) {
  html {
    font-size: 43.75%;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100%;
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  padding-bottom: 40px;
  margin-bottom: 40px;
  /* padding: 10px; */
}

body::before {
  content: '';
  position: fixed;
  left: 0;
  right: 0;
  z-index: -1;

  display: block;
  background-image: url(data:image/png;base64,);
  background-size: cover;
  width: 100%;
  height: 100%;

  -webkit-filter: blur(3px);
  -moz-filter: blur(3px);
  -o-filter: blur(3px);
  -ms-filter: blur(3px);
  filter: blur(3px);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

svg {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}

svg .state {
  stroke: #666;
  stroke-width: 1px;
}

svg .state:hover {
  stroke: #383874;
  stroke-width: 3px;
  cursor: pointer;
}

select {
  padding: 1rem 1.5rem;
  font-size: 1.3rem;
  border-radius: 6px;
}

.combo-select-year {
  position: absolute;
  top: 1rem;
  left: 1rem;
  z-index: 100000;
}

